.radioSuccess {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.radioLabel {
  display: inline-block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: $font-sm;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  line-height: 16px;
}

.radioButton {
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 10px;
  height: 16px;
  width: 16px;
  background-image: linear-gradient(to top,$white_gradient, $white);
  border: 1px solid $checkbox_border;
  border-radius: 50%;
}

.radioBusiness{
  margin-left: 40px;
}

.radioButton:after {
  content: "";
  position: absolute;
  display: none;
}

.radioLabel:hover input ~ .radioButton {
  background-color: #ccc;
}

.radioLabel input:checked ~ .radioButton {
  border: 1px solid $radio_green_border;
  background: linear-gradient(0deg, $radio_green_gradient_start 0%, $radio_green_gradient_end 100%);
  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.05);
}

.radioSuccess:after {
  content: "";
  position: absolute;
  display: none;
}

.radioLabel input:checked ~ .radioButton:after {
  display: block;
}

.radioLabel .radioButton:after {
  top: 2.8pt;
  left: 4px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: white;
}

