.checkDiv {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */
.checkDiv input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 16px;
  width: 16px;
  background-image: linear-gradient(to top,$white_gradient, $white);
  border: 1px solid $checkbox_border;
  border-radius: 4px;
}

/* On mouse-over, add a grey background image */
.checkDiv:hover input ~ .checkmark {
  background-image:  linear-gradient(to top, $white_gradient, $white);
  border: 1px solid $checkbox_border;
  border-radius: 4px;
}

/* When the checkbox is checked, add a blue background */
.checkDiv input:checked ~ .checkmark {
  background-image: linear-gradient(to top, $check_gradient_start, $check_gradient_end);
  border: 1px solid $check_gree_border;
  border-radius: 4px;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.checkDiv input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.checkDiv .checkmark:after {
  left: 4px;
  top: 1px;
  width: 5px;
  height: 10px;
  border: solid $white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

